import React from 'react';

class Monitorias extends React.Component {

    render() {

        return (
            <>
                <MonitoriasInterfaz/>
            </>
        );
    }
}

class MonitoriasInterfaz extends Monitorias {

    constructor(props){
        super(props);
        this.state = {
            Lista: [],
            showFormularioCrear: false
        }

        this.ControladorMonitoriaCrear = this.ControladorMonitoriaCrear.bind(this);
        this.RefrescarMonitorias = this.RefrescarMonitorias.bind(this);
    }

    RefrescarMonitorias() {

        let host = window.location.hostname;

        fetch("http://"+host+":5000/api/monitorias/lista")
            .then(res => res.json())
            .then(
                (result) => {
                    this.setState({Lista: result});
                },
                (error) => {
                    this.setState({Lista: []});
                }
            )
    }

    ControladorMonitoriaCrear() {

        this.setState({showFormularioCrear: !this.state.showFormularioCrear ? true : false});
        console.log("Evento cambia")
        console.log(this.state.showFormularioCrear);
    }

    ControladorMonitoriaCrearChange( estado ) {
        this.setState({showFormularioCrear: estado});
    }

    render() {

        return (
            <>
                <div className="row">
                    <h1 className="mt-4 col-md">Monitorias</h1>
                    <div className="col-md">
                        <div className="row mb-4 mt-4 justify-content-end">
                            { !this.state.showFormularioCrear ? <button className="btn rounded-pill btn-light mr-3" onClick={this.RefrescarMonitorias}><i className="fas fa-redo"></i>  Refrescar</button> : <></> }
                            { this.state.showFormularioCrear ? <button className="btn rounded-pill btn-light" onClick={() => {this.ControladorMonitoriaCrearChange(false)}}><i className="fas fa-list"></i>  Lista Monitores</button> : <></> }
                            { !this.state.showFormularioCrear ? <button className="btn rounded-pill btn-success" onClick={this.ControladorMonitoriaCrear}><i className="fas fa-user-plus"></i>  Crear Monitoria</button> : <></>}
                        </div>
                    </div>
                </div>

                {this.state.showFormularioCrear ? <MonitoriasCrear enlace={{
                    showFormularioCrear: this.state.showFormularioCrear,
                    ControladorMonitoriaCrearChange: this.ControladorMonitoriaCrearChange.bind(this)
                }}/> : <></>}
                {!this.state.showFormularioCrear ? <MonitoriasLista RefrescarMonitorias={this.RefrescarMonitorias} Lista={this.state.Lista} /> : <></>}
            </>
        );
    }
}

class MonitoriasEliminar extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            monitor: this.props.IdMonitoria.id_monitor,
            materia: this.props.IdMonitoria.id_materia
        }
        this.RefrescarMonitorias = this.props.RefrescarMonitorias;
        this.EliminarMonitoria =this.EliminarMonitoria.bind(this);
    }

    EliminarMonitoria(e) {

        e.preventDefault();
        console.log(this.state.monitor);
        console.log(this.state.materia);

        let host = window.location.hostname;
        fetch("http://"+host+":5000/api/monitorias", {
            method: 'DELETE',
            body: JSON.stringify({
                monitor: this.state.monitor,
                materia: this.state.materia
            }),
            headers: {'Content-Type': 'application/json'}
        })
            .then(res => res.json())
            .then(
                (result) => {
                    console.log(result)
                    switch( result.mensaje ){
                        case 1:
                            this.RefrescarMonitorias();
                            break;
                        default:
                            break;
                    }
                }
            );
    }

    render() {

        return (
            <>
                <div onClick={this.EliminarMonitoria} className="btn btn-danger rounded-circle" title="Eliminar"><i className="fas fa-times"></i></div>
            </>
        );
    }
}

class MonitoriasModificar extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            showFormularioModificar: false
        }
        this.RefrescarMonitorias = this.props.RefrescarMonitorias;
        this.showFormularioModificar = this.showFormularioModificar.bind(this);
    }

    setShowFormularioModificar( estado ) {

        this.setState({showFormularioModificar: estado});
    }

    showFormularioModificar() {

        console.log("Abrir interfaz modificar");
        this.props.enlace.ControladorMonitoriasModificarChange(true, this.props.InfoMonitoria)
    }

    render() {

        return (
            <>
                <div onClick={this.showFormularioModificar} className="btn btn-light rounded-circle mr-3" title="Editar"><i className="fas fa-edit"></i></div>
            </>
        );
    }
}

class MonitoriasInterfazModificar extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            monitores: [],
            materias: [],
            respuesta: "",
            materia: this.props.monitoria.id_materia,
            salon: this.props.monitoria.salon,
            fecha: this.props.monitoria.fecha,
            monitor: this.props.monitoria.id_monitor,
            original: {
                monitor: this.props.monitoria.id_monitor,
                materia: this.props.monitoria.id_materia
            }
        }

        this.Cancelar = this.Cancelar.bind(this);
        this.ModificarMonitoria = this.ModificarMonitoria.bind(this);
        this.setMateria = this.setMateria.bind(this);
        this.setSalon = this.setSalon.bind(this);
        this.setFecha = this.setFecha.bind(this);
        this.setMonitor = this.setMonitor.bind(this);
    }

    // Setters y Getters de los estados del formulario

    setMateria(evento) {
        this.setState({materia: evento.target.value});
    }
    getMateria() {
        return this.state.materia;
    }

    setSalon(evento) {
        this.setState({salon: evento.target.value});
    }
    getSalon() {
        return this.state.salon;
    }

    setFecha(evento) {
        this.setState({fecha: evento.target.value});
    }
    getFecha() {
        return this.state.fecha;
    }

    setMonitor(evento) {
        this.setState({monitor: evento.target.value});
    }
    getMonitor() {
        return this.state.monitor;
    }

    Cancelar(e) {

        e.preventDefault();
        this.props.enlace.ControladorMonitoriasModificarChange(false);
    }

    componentDidMount() {

        let host = window.location.hostname;

        fetch("http://"+host+":5000/api/monitorias/monitores")
        .then(res => res.json())
        .then(
            (result) => {
                console.log(result);
                this.setState({monitores: result});
            },
            (error) => {
                this.setState({monitores: []});
            }
        )

        fetch("http://"+host+":5000/api/monitorias/materias")
        .then(res => res.json())
        .then(
            (result) => {
                console.log(result);
                this.setState({materias: result});
            },
            (error) => {
                this.setState({materias: []});
            }
        )
    }

    ModificarMonitoria(e) {

        e.preventDefault();

        let Materia = parseInt(this.getMateria());
        let Salon = this.getSalon();
        let Fecha = this.getFecha();
        let Monitor = parseInt(this.getMonitor());


        if( Materia.length <= 0 || Salon.length <= 0 || Fecha.length <= 0 || Monitor.length <= 0 ){

            console.log({
                materia: Materia,
                salon: Salon,
                fecha: Fecha,
                monitor: Monitor
            });
            return false;
        }

        console.log("Enviando formulario")
        let host = window.location.hostname;

        fetch("http://"+host+":5000/api/monitorias", {
            method: 'PUT',
            body: JSON.stringify({
                nuevo: {
                    materia: Materia,
                    salon: Salon,
                    fecha: Fecha,
                    monitor: Monitor
                },
                original: this.state.original
            }),
            headers: {'Content-Type': 'application/json'}
        })
            .then(res => res.json())
            .then(
                (result) => {
                    this.setState({respuesta: result});
                    console.log(result)
                    switch( result.mensaje ){
                        case 1:
                            this.props.enlace.ControladorMonitoriasModificarChange(false)
                            break;
                        default:
                            break;
                    }
                },
                (error) => {
                    this.setState({respuesta: error});
                }
            )
    }

    render() {
        return (
            <>
                <form className="was-validated">

                    <div className="form-row align-items-center">
                        <h3 className="mr-4 col-md-3 text-center"><i className="fas fa-user-edit"></i> Monitoria</h3>
                        <div className="col">
                            <label>Monitor</label>
                            <div className="input-group">
                                <select className="custom-select" value={this.state.monitor} onChange={this.setMonitor} autoComplete="off" required>
                                    <MonitoriasCrearOpciones opciones={this.state.monitores}/>
                                </select>
                                <div className="invalid-feedback">
                                    Porfavor seleccione una un monitor.
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className="form-row mt-2">
                        <div className="col-md">
                            <label>Materia</label>
                            <div className="input-group">
                                <select className="custom-select" value={this.state.materia} onChange={this.setMateria} autoComplete="off" required>
                                    <MonitoriasCrearOpciones opciones={this.state.materias}/>
                                </select>
                                <div className="invalid-feedback">
                                    Porfavor seleccione una materia.
                                </div>
                            </div>
                        </div>

                        <div className="col-md">
                            <label>Salon</label>
                            <div className="input-group">
                                <input type="text" className="form-control fix-rounded-right" value={this.state.salon} onChange={this.setSalon} autoComplete="off" required/>
                                <div className="invalid-feedback">
                                    Porfavor ingrese un salon.
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className="form-row mt-2">
                        <div className="col-md">
                            <label>Fecha</label>
                            <div className="input-group">
                                <div className="input-group-prepend">
                                    <span className="input-group-text">
                                        <i className="fas fa-chalkboard-teacher"></i>
                                    </span>
                                </div>
                                <input type="date" className="form-control fix-rounded-right" value={this.state.fecha} onChange={this.setFecha} autoComplete="off" required/>
                                <div className="invalid-feedback">
                                    Porfavor seleccione una fecha.
                                </div>
                            </div>
                        </div>

                        <div className="col-md">
                        </div>
                    </div>

                    <div className="form-row justify-content-center mt-3 mb-4">
                        <button className="col-md-4 btn btn-danger" onClick={this.Cancelar}>Cancelar</button>
                        <input type="submit" onClick={this.ModificarMonitoria} value="Guardar" className="col-md-4 btn btn-primary"/>
                    </div>
                </form>
            </>
        );
    }
}

