function MonitoriasCrearOpciones(props) {

    const opciones = props.opciones.map((fila, key) =>

        <option key={key} value={fila.id}>{fila.nombre}</option>
    );

    return (
        <>
            {opciones}
        </>
    );
}

class MonitoriasLista extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            showFormularioModificar: false,
            monitaria: {
                monitor: "",
                salon: "",
                fecha: "",
                materia: ""
            }
        }
        this.RefrescarMonitorias = this.props.RefrescarMonitorias;

        this.ControladorMonitoriasModificarChange = this.ControladorMonitoriasModificarChange.bind(this);
    }

    componentDidMount() {

        // Pidiendo datos lista de monitores
        this.RefrescarMonitorias();
    }

    ControladorMonitoriasModificarChange(estado, monitoria) {

        console.log(monitoria);
        this.setState({showFormularioModificar: estado, monitoria: monitoria});
        // Pidiendo datos lista de monitores
        this.RefrescarMonitorias();
    }

    render() {
        return (
            <>
                { !this.state.showFormularioModificar ? <div className="row">
                    <TablaLista RefrescarMonitorias={this.RefrescarMonitorias} Lista={this.props.Lista} enlace={{
                        showFormularioModificar: this.showFormularioModificar,
                        ControladorMonitoriasModificarChange: this.ControladorMonitoriasModificarChange.bind(this)
                    }}/>
                </div>: <></>}
                { this.state.showFormularioModificar ? <MonitoriasInterfazModificar monitoria={this.state.monitoria} enlace={{
                    showFormularioModificar: this.showFormularioModificar,
                    ControladorMonitoriasModificarChange: this.ControladorMonitoriasModificarChange.bind(this)
                }}/> : <></>}
            </>
        );
    }
}

function TablaLista(props) {


    // La cabecera
    const DatosCabecera = ['Materias', 'Salon', 'Fecha', '', ''];

    // Estos son los monitores traidos de la base de datos
    const ListaMonitores = props.Lista;

    console.log(ListaMonitores);

    // Armando fila de la cabecera de la tabla
    const CabeceraTabla = DatosCabecera.map( (col, key) => {
        switch(key){
            case DatosCabecera.length - 1:
                return  (<td key={key} className="text-center"><i className="fas fa-cog"></i></td>);
            case DatosCabecera.length - 2:
                return  (<td key={key} className="text-center" title="Monitores"><i className="fas fa-users"></i></td>);
            default:
                return  (<td key={key}><strong>{col}</strong></td>);
        }
    });

    // Armando fila contenido de la tabla
    const ListaCuerpoTabla = ListaMonitores.map( (fila, key) =>
        <tr key={key.toString()}>
            <td>{fila.materia}</td>
            <td>{fila.salon}</td>
            <td>{fila.fecha}</td>
            <td className="text-center">{fila.monitor}</td>
            <td className="text-center">
                <MonitoriasModificar RefrescarMonitorias={props.RefrescarMonitorias} InfoMonitoria={fila} enlace={props.enlace}/>
                <MonitoriasEliminar RefrescarMonitorias={props.RefrescarMonitorias} IdMonitoria={fila}/>
            </td>
        </tr>
    );

    return (
        <>
            <table className='table table-light table-bordered'>
                <thead>
                    <tr>
                        {CabeceraTabla}
                    </tr>
                </thead>
                <tbody>
                    {ListaCuerpoTabla}
                </tbody>
            </table>
        </>
    );
}

export default Monitorias;