class MonitoresLista extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            showFormularioModificar: false,
            monitor: {
                cedula: "",
                nombres: "",
                apellidos: "",
                programa_academico: "",
                semestre: "",
                telefono: "",
                email: ""
            }
        }
        this.RefrescarMonitores = this.props.RefrescarMonitores;

        this.ControladorMonitorModificarChange = this.ControladorMonitorModificarChange.bind(this);
    }

    componentDidMount() {

        // Pidiendo datos lista de monitores
        this.RefrescarMonitores();
    }

    ControladorMonitorModificarChange(estado, monitor) {

        this.setState({showFormularioModificar: estado, monitor: monitor});

        // Pidiendo datos lista de monitores
        this.RefrescarMonitores();
    }

    render() {
        return (
            <>
                { !this.state.showFormularioModificar ? <div className="row">
                    <TablaLista RefrescarMonitores={this.RefrescarMonitores} Lista={this.props.Lista} enlace={{
                        showFormularioModificar: this.showFormularioModificar,
                        ControladorMonitorModificarChange: this.ControladorMonitorModificarChange.bind(this)
                    }}/>
                </div>: <></>}
                { this.state.showFormularioModificar ? <MonitoresInterfazModificar monitor={this.state.monitor} enlace={{
                    showFormularioModificar: this.showFormularioModificar,
                    ControladorMonitorModificarChange: this.ControladorMonitorModificarChange.bind(this)
                }}/> : <></>}
            </>
        );
    }
}

function TablaLista(props) {


    // La cabecera
    const DatosCabecera = ['Cedula', 'Nombre', 'Apellido', '', '', ''];

    // Estos son los monitores traidos de la base de datos
    const ListaMonitores = props.Lista;

    console.log(ListaMonitores);

    // Armando fila de la cabecera de la tabla
    const CabeceraTabla = DatosCabecera.map( (col, key) => {
        switch(key){
            case DatosCabecera.length - 1:
                return  (<td key={key} className="text-center"><i className="fas fa-cog"></i></td>);
            case DatosCabecera.length - 2:
                return  (<td key={key} className="text-center" title="Semestre"><i className="fas fa-graduation-cap"></i></td>);
            case DatosCabecera.length - 3:
                return  (<td key={key} className="text-center" title="Programa Academico"><i className="fas fa-chalkboard-teacher"></i></td>);
            default:
                return  (<td key={key}><strong>{col}</strong></td>);
        }
    });

    // Armando fila contenido de la tabla
    const ListaCuerpoTabla = ListaMonitores.map( (fila, key) =>
        <tr key={key.toString()}>
            <td>{fila.cedula}</td>
            <td>{fila.nombres}</td>
            <td>{fila.apellidos}</td>
            <td className="text-center">{fila.programa_academico}</td>
            <td className="text-center">{fila.semestre}</td>
            <td className="text-center">
                <MonitoresModificar RefrescarMonitores={props.RefrescarMonitores} IdMonitor={fila.cedula} InfoMonitor={fila} enlace={props.enlace}/>
                <MonitoresEliminar RefrescarMonitores={props.RefrescarMonitores} IdMonitor={fila.cedula}/>
            </td>
        </tr>
    );

    return (
        <>
            <table className='table table-light table-bordered'>
                <thead>
                    <tr>
                        {CabeceraTabla}
                    </tr>
                </thead>
                <tbody>
                    {ListaCuerpoTabla}
                </tbody>
            </table>
        </>
    );
}

export default Monitores;