import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route
} from 'react-router-dom';

// Librerias necesarias para la plantilla
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min.js';

import '../src/css/styles.css';
import Imagen404 from './assets/img/error-404-monochrome.svg'
import './App.css';

// Componentes de la plantilla
import HeaderBarra from './modelo/Header/HeaderBarra';
import SideMenu from './modelo/Side/SideMenu';
import Content from './modelo/Content/Content';
import Footer from './modelo/Footer/Footer';

// Vistas
import Monitores from './modelo/Monitores/Monitores';
import Monitorias from './modelo/Monitorias/Monitorias';
import Home from './modelo/Home/Home';

// Prueba bucles

function App() {
  return (
    <>
      <Router>
        <div className="App">
          <header className="App-header row m-0 p-0">
          <div className="col p-0">
            <HeaderBarra/>
            <div id="layoutSidenav">
            <SideMenu />
            <Content>
                <Switch>
                  <Route exact path="/" component={Home}>
                  </Route>
                  <Route path="/Monitores" component={Monitores}>
                  </Route>
                  <Route path="/Monitorias" component={Monitorias}>
                  </Route>
                  <Route path="*" component={NotMatch}>
                  </Route>
                </Switch>
            </Content>
            </div>
          </div>
          </header>
          <div id="layoutSidenav_content">
            <footer className="py-4 bg-light mt-auto"><Footer/></footer>
          </div>
        </div>
      </Router>
    </>
  );
}

function NotMatch() {

  return (
    <>
      <div className="row align-item-center mt-4">
        <img className="col" src={Imagen404} alt="logo" height="200" width="200"/>
      </div>
    </>
  );
}

export default App;
