import React from 'react';
import {Link} from 'react-router-dom';
//import App from "../../App";
import $ from 'jquery';

function SideMenu() {

    // Propiedades del menu

    // Add active state to sidbar nav links
    let path = window.location.href; // because the 'href' property of the DOM element is the absolute path
    console.log("Path window");
    console.log(path);
    $("#layoutSidenav_nav .sb-sidenav a.nav-link").each(function(e) {

        console.log(this.href);
        console.log(e);
        if (this.href === path) {
            console.log("Path ...")
            $(this).addClass("active");
        }
    });

    return (
        <div id="layoutSidenav_nav">
            <nav className="sb-sidenav accordion sb-sidenav-dark" id="sidenavAccordion">
                <div className="sb-sidenav-menu">
                    <div className="nav">
                        <div className="sb-sidenav-menu-heading">Core</div>
                        <Link className="nav-link" to="/#">
                            <div className="sb-nav-link-icon"><i className="fas fa-tachometer-alt"></i></div>
                            Dashboard
                        </Link>
                        <div className="sb-sidenav-menu-heading">Interface</div>
                        <Link className="nav-link" to="/Monitores">
                            <div className="sb-nav-link-icon"><i className="fas fa-users"></i></div>
                            Monitores
                        </Link>
                        <Link className="nav-link" to="/Monitorias">
                            <div className="sb-nav-link-icon"><i className="fas fa-clipboard-list"></i></div>
                            Monitorias
                        </Link>
                    </div>
                </div>
                <div className="sb-sidenav-footer">
                    <div className="small">Software de Monitorias</div>
                    Prueba React by ucros
                </div>
            </nav>
        </div>
    );
}

export default SideMenu;