import React from 'react';
import $ from 'jquery';

function HeaderBarra() {

    function SwitchMenuNavegacion(e) {

        e.preventDefault();
        $("body").toggleClass("sb-sidenav-toggled");
    }

    return (
        <>
            <nav className="sb-topnav navbar navbar-expand navbar-dark bg-dark">
                <a className="navbar-brand" href="/#">Software de Monitorias</a>
                <button className="btn btn-link btn-sm order-1 order-lg-0" id="sidebarToggle"  onClick={SwitchMenuNavegacion} href="#"><i className="fas fa-bars"></i></button>
            </nav>
        </>
    );
}

export default HeaderBarra;